body {
	overflow-x: hidden;
}

.app-header {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 0.5rem 1rem;
}
.app-header > .container,
.app-header > .container-fluid {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
}
@media (max-width: 767.98px) {
	.app-header > .container,
.app-header > .container-fluid {
		padding-right: 0;
		padding-left: 0;
	}
}
@media (min-width: 768px) {
	.app-header {
		flex-flow: row nowrap;
		justify-content: flex-start;
	}
	.app-header > .container,
.app-header > .container-fluid {
		flex-wrap: nowrap;
	}
}

.app-content {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}
.app-content::after {
	display: block;
	clear: both;
	content: "";
}
@media (min-width: 576px) {
	.app-content {
		max-width: 540px;
	}
}
@media (min-width: 768px) {
	.app-content {
		max-width: 720px;
	}
}
@media (min-width: 992px) {
	.app-content {
		max-width: 960px;
	}
}
@media (min-width: 1200px) {
	.app-content {
		max-width: 1140px;
	}
}

.app-footer .widget-area {
	padding-top: 5rem;
	padding-bottom: 2rem;
	background-color: #f8f9fa;
}
.app-footer__credit {
	padding-top: 2rem;
	padding-bottom: 2rem;
}
@media (max-width: 991.98px) {
	.app-footer .widget-column {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.comments__list {
	padding-left: 0;
	list-style: none;
}
.comments__list .children {
	list-style: none;
}
.menu--primary {
	flex-basis: 100%;
	flex-grow: 1;
	align-items: center;
	border-bottom: 1px solid rgba(0, 0, 0, 0.15);
	display: none;
}
.menu.toggled {
	display: block;
}
.menu__items {
	display: flex;
	flex-direction: column;
	padding-left: 0;
	margin-bottom: 0;
	list-style: none;
}
.menu__sub-menu {
	position: static;
	float: none;
	top: 100%;
	right: 0;
	left: auto;
	z-index: 1000;
	display: none;
	min-width: 10rem;
	padding-left: 0;
	margin: 0 1rem 0;
	text-align: left;
	list-style: none;
	background-color: #fff;
	background-clip: padding-box;
}
.menu__sub-menu ul {
	top: 0;
	right: 100%;
}
.menu__sub-menu li {
	display: block;
	width: 100%;
}
.menu__item {
	clear: both;
	position: relative;
	border-top: 1px solid rgba(0, 0, 0, 0.15);
}
.menu__item:hover > ul, .menu__item.focus > ul {
	display: block;
}
.menu__link {
	display: block;
	text-decoration: none;
	color: #1a1a1a;
	line-height: 1.3125;
	outline-offset: -1px;
	padding: 0.84375em 0;
}
@media (min-width: 768px) {
	.menu--primary {
		display: flex !important;
		flex-basis: auto;
		border: 0;
	}
	.menu__items {
		flex-direction: row;
		margin-left: auto;
	}
	.menu__sub-menu {
		position: absolute;
		margin: 0;
		border-bottom: 1px solid rgba(0, 0, 0, 0.15);
	}
	.menu__sub-menu:before, .menu__sub-menu:after {
		border-style: solid;
		content: "";
		position: absolute;
	}
	.menu__sub-menu:before {
		border-color: #d1d1d1 transparent;
		border-width: 0 10px 10px;
		right: 9px;
		top: -9px;
	}
	.menu__sub-menu:after {
		border-color: #fff transparent;
		border-width: 0 8px 8px;
		right: 11px;
		top: -7px;
	}
	.menu__sub-menu ul:before, .menu__sub-menu ul::after {
		border: 0;
	}
	.menu__sub-menu li {
		border: 1px solid rgba(0, 0, 0, 0.15);
		border-bottom-width: 0;
	}
	.menu__item {
		border: 0;
	}
}

.menu-toggle {
	padding: 0.25rem 0.75rem;
	font-size: 1.25rem;
	line-height: 1;
	background-color: transparent;
	border: 1px solid transparent;
	border-radius: 0.25rem;
}
.menu-toggle:hover, .menu-toggle:focus {
	text-decoration: none;
}
.menu-toggle:not(:disabled):not(.disabled) {
	cursor: pointer;
}

.menu-toggle-icon {
	display: inline-block;
	width: 1.5em;
	height: 1.5em;
	vertical-align: middle;
	content: "";
	background: no-repeat center center;
	background-size: 100% 100%;
}

.menu-toggle-icon {
	background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

@media (min-width: 768px) {
	.menu-toggle {
		display: none;
	}
}
.widget {
	margin-bottom: 3rem;
}
.widget__title {
	font-size: 0.875rem;
	text-transform: uppercase;
}
.widget ul {
	padding-left: 0;
	list-style: none;
}

.wp-block-audio {
	margin: 0;
}
.wp-block-audio audio {
	width: 100%;
}

.wp-block-button {
	margin-bottom: 1.5em;
}
.wp-block-button__link {
	border: none;
	border-radius: 0;
	box-shadow: none;
	cursor: pointer;
	display: inline-block;
	font-size: 18px;
	line-height: 24px;
	margin: 0;
	padding: 11px 24px;
	text-align: center;
	text-decoration: none;
	white-space: normal;
	word-break: break-all;
}

.wp-block-columns {
	display: flex;
}
.wp-block-column {
	flex: 1;
}

.editor-inner-blocks {
	flex: 1;
}

.wp-block-cover {
	position: relative;
	background-size: cover;
	background-position: 50%;
	min-height: 430px;
	width: 100%;
	margin: 0 0 1.5em;
	display: flex;
	justify-content: center;
	align-items: center;
}
.wp-block-cover.has-parallax {
	background-attachment: fixed;
}
.wp-block-cover.has-background-dim::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}
.wp-block-cover.has-background-dim-10::before {
	background-color: rgba(52, 58, 64, 0.1);
}
.wp-block-cover.has-background-dim-20::before {
	background-color: rgba(52, 58, 64, 0.2);
}
.wp-block-cover.has-background-dim-30::before {
	background-color: rgba(52, 58, 64, 0.3);
}
.wp-block-cover.has-background-dim-40::before {
	background-color: rgba(52, 58, 64, 0.4);
}
.wp-block-cover.has-background-dim-50::before {
	background-color: rgba(52, 58, 64, 0.5);
}
.wp-block-cover.has-background-dim-60::before {
	background-color: rgba(52, 58, 64, 0.6);
}
.wp-block-cover.has-background-dim-70::before {
	background-color: rgba(52, 58, 64, 0.7);
}
.wp-block-cover.has-background-dim-80::before {
	background-color: rgba(52, 58, 64, 0.8);
}
.wp-block-cover.has-background-dim-90::before {
	background-color: rgba(52, 58, 64, 0.9);
}
.wp-block-cover.has-background-dim-100::before {
	background-color: #343a40;
}
.wp-block-cover.has-left-content {
	justify-content: flex-start;
}
.wp-block-cover.has-right-content {
	justify-content: flex-end;
}
.wp-block-cover__video-background {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 50%;
	left: 50%;
	object-fit: fill;
	transform: translateX(-50%) translateY(-50%);
	z-index: 1;
}

.wp-block-cover-text {
	color: #fff;
	font-size: 2em;
	line-height: 1.25;
	z-index: 1;
	margin-bottom: 0;
	max-width: 750px;
	padding: 14px;
	text-align: center;
}
.wp-block-cover-text a,
.wp-block-cover-text a:hover,
.wp-block-cover-text a:focus,
.wp-block-cover-text a:active {
	color: #fff;
}

.has-left-content .wp-block-cover-text {
	margin-left: 0;
	text-align: left;
}

.has-right-content .wp-block-cover-text {
	margin-right: 0;
	text-align: right;
}

.wp-block-embed figcaption {
	margin-top: 0.5em;
	text-align: center;
}

.wp-block-file {
	margin-bottom: 1.5em;
}
.wp-block-file.aligncenter {
	text-align: center;
}
.wp-block-file.alignright {
	text-align: right;
}
.wp-block-file__button {
	background: #343a40;
	color: #fff;
	padding: 0.5em 1em;
	text-decoration: none;
}
.wp-block-file__button:hover, .wp-block-file__button:visited, .wp-block-file__button:focus, .wp-block-file__button:active {
	color: #fff;
	opacity: 0.85;
	text-decoration: none;
}

* + .wp-block-file__button {
	margin-left: 0.75em;
}

.wp-block-gallery {
	display: flex;
	flex-wrap: wrap;
	list-style-type: none;
	padding: 0;
	margin: 0 -8px 0 -8px;
}

.blocks-gallery-item {
	margin: 8px;
	display: flex;
	flex-grow: 1;
	flex-direction: column;
	justify-content: center;
	position: relative;
}
.blocks-gallery-item figure {
	margin: 0;
	height: 100%;
}
@supports (position: sticky) {
	.blocks-gallery-item figure {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
	}
}
.blocks-gallery-item img {
	display: block;
	max-width: 100%;
	height: auto;
	width: 100%;
}
@supports (position: sticky) {
	.blocks-gallery-item img {
		width: auto;
	}
}
.blocks-gallery-item figcaption {
	position: absolute;
	bottom: 0;
	width: 100%;
	max-height: 100%;
	padding: 40px 10px 5px;
	text-align: center;
	color: #fff;
	background: linear-gradient(0deg, rgba(52, 58, 64, 0.7) 0, rgba(52, 58, 64, 0.3) 60%, transparent);
}
.blocks-gallery-item figcaption img {
	display: inline;
}

.is-cropped .blocks-gallery-item a,
.is-cropped .blocks-gallery-item img {
	flex: 1;
	width: 100%;
}
@supports (position: sticky) {
	.is-cropped .blocks-gallery-item a,
.is-cropped .blocks-gallery-item img {
		flex: 1;
		height: 100%;
		object-fit: cover;
	}
}

.blocks-gallery-item {
	width: calc( 100% / 2 - 16px );
}

.columns-1 .blocks-gallery-item {
	width: calc( 100% / 1 - 16px );
}

@media (min-width: 600px) {
	.columns-3 .blocks-gallery-item {
		width: calc( 100% / 3 - 16px );
	}

	.columns-4 .blocks-gallery-item {
		width: calc( 100% / 4 - 16px );
	}

	.columns-5 .blocks-gallery-item {
		width: calc( 100% / 5 - 16px );
	}

	.columns-6 .blocks-gallery-item {
		width: calc( 100% / 6 - 16px );
	}

	.columns-7 .blocks-gallery-item {
		width: calc( 100% / 7 - 16px );
	}

	.columns-8 .blocks-gallery-item {
		width: calc( 100% / 8 - 16px );
	}
}
.wp-block-image {
	width: fit-content;
	max-width: 100%;
}
.wp-block-image img {
	max-width: 100%;
}
.wp-block-image.is-resized {
	width: min-content;
	display: -ms-inline-grid;
	-ms-grid-columns: min-content;
}
.wp-block-image.is-resized figcaption {
	-ms-grid-row: 2;
}
.wp-block-image.is-resized img {
	max-width: none;
}
.wp-block-latest-posts.is-grid {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	list-style: none;
}
.wp-block-latest-posts.is-grid li {
	margin: 0 16px 16px 0;
	width: 100%;
}
@media (min-width: 600px) {
	.wp-block-latest-posts.columns-2 li {
		width: calc( ( 100% / 2 ) - 16px );
	}
	.wp-block-latest-posts.columns-3 li {
		width: calc( ( 100% / 3 ) - 16px );
	}
	.wp-block-latest-posts.columns-4 li {
		width: calc( ( 100% / 4 ) - 16px );
	}
	.wp-block-latest-posts.columns-5 li {
		width: calc( ( 100% / 5 ) - 16px );
	}
	.wp-block-latest-posts.columns-6 li {
		width: calc( ( 100% / 6 ) - 16px );
	}
}
.wp-block-latest-posts__post-date {
	display: block;
}

.wp-block-media-text {
	display: grid;
	grid-template-rows: auto;
	align-items: center;
	grid-template-areas: "media-text-media media-text-content";
	grid-template-columns: 50% auto;
}
.wp-block-media-text.has-media-on-the-right {
	grid-template-areas: "media-text-content media-text-media";
	grid-template-columns: auto 50%;
}
.wp-block-media-text__media {
	grid-area: media-text-media;
	margin: 0;
}
.wp-block-media-text__content {
	word-break: break-word;
	grid-area: media-text-content;
	padding: 0 8%;
}
.wp-block-media-text > figure > img,
.wp-block-media-text > figure > video {
	max-width: unset;
	width: 100%;
	vertical-align: middle;
}

@media (max-width: 600px) {
	.wp-block-media-text.is-stacked-on-mobile {
		grid-template-columns: 100% !important;
		grid-template-areas: "media-text-media" "media-text-content";
	}

	.wp-block-media-text.is-stacked-on-mobile.has-media-on-the-right {
		grid-template-areas: "media-text-content" "media-text-media";
	}
}
.wp-block-pullquote {
	padding: 3em 0;
	text-align: center;
}
.wp-block-pullquote cite,
.wp-block-pullquote footer {
	position: relative;
}

.wp-block-subhead {
	font-size: 1.125em;
	font-style: italic;
	opacity: 0.75;
}

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	word-wrap: normal;
	border: none;
}
.screen-reader-text:active, .screen-reader-text:focus {
	clip: auto;
	clip-path: none;
	display: block;
	height: auto;
	width: auto;
	z-index: 100000;
	text-decoration: none;
}

@media (min-width: 375px) {
	.alignleft {
		float: left;
		margin: 0 1.5rem 1.5rem 0;
		max-width: 50%;
	}
}

@media (min-width: 375px) {
	.alignright {
		float: left;
		margin: 0 0 1.5rem 1.5rem;
