	}
}

.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	clear: both;
}

.alignfull,
.alignwide {
	margin-left: calc( -100vw / 2 + 100% / 2 );
	margin-right: calc( -100vw / 2 + 100% / 2 );
	width: auto;
	max-width: 100vw;
}

@media (min-width: 750px) {
	.alignwide {
		margin-left: -25%;
		margin-right: -25%;
		max-width: calc( 750px + 50% );
	}
}
